	module.exports = {"data":{"post":{"html":"<p>Some moments the world seems perfect. Like last evening. When the weather is good, Hamburg is one of the nicest cities in the world. Of course, the weather is good only 10 days per year. But last night the weather was perfect. 25&deg;C. sunny. slight breeze. We took Christopher and his grandparents to the <a href=\"/2000/07/23/1313.php\">Hamburger Dom</a>, where he rode the kiddie train at least 10 times. And driving home, the rush hour traffic had dispersed, the sun-roof was open, the radio was blasting, and I thought to myself that the moment was perfect. And it was.</p>","fields":{"title":"Perfect World","date":"17 Aug 2002","url":"https://papascott.de/2002-08-17-perfect-world/"}}},"pathContext":{"slug":"/2002-08-17-perfect-world/","prev":{"fields":{"title":"Lost In The Flood","date":"16 Aug 2002","slug":"/2002-08-16-lost-in-the-flood/"}},"next":{"fields":{"title":"Vacation Weekend","date":"18 Aug 2002","slug":"/2002-08-18-vacation-weekend/"}}}}

